import * as TWEEN from '@tweenjs/tween.js';
import * as THREE from 'three';
import dimensions from './dimensions';
import Player from './player';
import view from './view';

const camera = new THREE.OrthographicCamera(
    -view.width / 2,
    view.width / 2,
    view.height / 2,
    -view.height / 2,
);

const listener = new THREE.AudioListener();
const audio = new THREE.Audio(listener);
const analyser = new THREE.AudioAnalyser(audio, 32);
camera.add(listener);

function handleSuccess(stream: MediaStream) {
    const context = listener.context;
    const source = context.createMediaStreamSource(stream);
    audio.setMediaStreamSource(source.mediaStream);
    source.connect(analyser.analyser);
    console.log('Audio successfully attached!');
}

navigator.mediaDevices.getUserMedia({
    audio: {
        autoGainControl: false,
        channelCount: 2,
        echoCancellation: false,
        latency: 0,
        noiseSuppression: false,
    } as MediaTrackConstraints,
})
    .then(handleSuccess)
    .catch(console.error);

const findFundamentalFreq = (buffer: Uint8Array, sampleRate: number): number =>
{
    // We use Autocorrelation to find the fundamental frequency.

    // In order to correlate the signal with itself (hence the name of the algorithm), we will check two points 'k' frames away.
    // The autocorrelation index will be the average of these products. At the same time, we normalize the values.
    // Source: http://www.phy.mty.edu/~suits/autocorrelation.html
    // Assuming the sample rate is 48000Hz, a 'k' equal to 1000 would correspond to a 48Hz signal (48000/1000 = 48),
    // while a 'k' equal to 8 would correspond to a 6000Hz one, which is enough to cover most (if not all)
    // the notes we have in the notes.json file.
    let n = 1024, bestR = 0, bestK = -1;
    for (let k = 8; k <= 1000; k++) {
        let sum = 0;

        for (let i = 0; i < n; i++) {
            sum += ((buffer[i] - 128) / 128) * ((buffer[i + k] - 128) / 128);
        }

        const r = sum / (n + k);
        console.log(r);

        if (r > bestR) {
            bestR = r;
            bestK = k;
        }

        if (r > 0.9) {
            // Let's assume that this is good enough and stop right here
            break;
        }
    }

    if (bestR > 0.0025) {
        // The period (in frames) of the fundamental frequency is 'bestK'. Getting the frequency from there is trivial.
        const fundamentalFreq = sampleRate / bestK;
        return fundamentalFreq;
    }
    else {
        // We haven't found a good correlation
        return -1;
    }
};

